.communityRightbar {
    flex: 3;
  
    position: sticky;
    top: 70px;
    height: calc(100vh - 70px);
    overflow: scroll;
   
   

    &::-webkit-scrollbar {
        display: none;
    }

    .communityContainer { 
        .section-right {
       
        display: flex;
        flex-direction: column;
        gap: 1rem;
        .item {
        
            background: #ffffff;
                box-shadow: 0px 26px 84px rgb(0 0 0 / 7%);
                border-radius: 10px;
                padding: 1rem;
        
                .span {
                    color: gray;
                    font-size: 1.3rem;
                    align-items: center;
                    justify-content: space-between;
                    font-weight: 500;
                }
        
                .user {
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    margin: 20px 0;
        
                    .userInfo {
                        display: flex;
                        align-items: center;
                        gap: 7px;
        
                    }
        
                    img {
                        width: 50px;
                        height: 50px;
                        border-radius: 50%;
                        object-fit: cover;
                    }
        
        
                    .buttons {
                        display: flex;
                        align-items: center;
                        gap: 10px;
                    }
        
                    button {
                        padding: 5px;
                        color: white;
                        cursor: pointer;
                        background: var(--pink);
                        border-radius: 0.5rem;
        
                        transition: all 100ms ease-out;
                        font-size: 15px;
                        padding: 6px 9px;
        
                    }
        
        
        
                }
        
            }
               
        }
        

    }

}